@font-face {
  font-family: "Lato";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("Lato-Regular"), url("../fonts/Lato-Regular.woff") format("woff"), url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Lato";
  font-display: swap;
  font-weight: 300;
  font-style: normal;
  src: local("Lato-Light"), url("../fonts/Lato-Light.woff") format("woff"), url("../fonts/Lato-Light.woff2") format("woff2"), url("../fonts/Lato-Light.ttf") format("truetype");
}
@font-face {
  font-family: "Lato";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: local("Lato-Bold"), url("../fonts/Lato-Bold.woff") format("woff"), url("../fonts/Lato-Bold.woff2") format("woff2"), url("../fonts/Lato-Bold.ttf") format("truetype");
}
.container {
  margin: 0 auto;
  max-width: 95vw;
  min-height: 5rem;
}
@media (min-width: 270px) {
  .container {
    max-width: 95%;
  }
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}

.wrapper {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-around;
  padding: 2rem;
}

[class*=col-] {
  display: block;
}

.col-1 {
  width: calc((100% / 1) - 1rem);
}

.col-2 {
  width: calc((100% / 2) - 1rem);
}

.col-3 {
  width: calc((100% / 3) - 1rem);
}

.col-4 {
  width: calc((100% / 4) - 1rem);
}

.col-5 {
  width: calc((100% / 5) - 1rem);
}

.col-6 {
  width: calc((100% / 6) - 1rem);
}

.col-7 {
  width: calc((100% / 7) - 1rem);
}

.col-8 {
  width: calc((100% / 8) - 1rem);
}

.col-9 {
  width: calc((100% / 9) - 1rem);
}

.col-10 {
  width: calc((100% / 10) - 1rem);
}

.col-11 {
  width: calc((100% / 11) - 1rem);
}

.col-12 {
  width: calc((100% / 12) - 1rem);
}

:root {
  box-sizing: border-box;
  font-size: 22px;
  line-height: 36px;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

html,
body {
  font-family: "Lato", Arial, sans-serif;
  font-weight: 400;
  font-size: inherit;
  line-height: inherit;
  scroll-behavior: smooth;
  color: #fff;
  background-color: #030203;
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
b {
  font-weight: inherit;
  font-size: inherit;
}

/* ----- menu media ----- */
body._lock {
  overflow: hidden;
}

@media (max-width: 991px) {
  .menu__burger {
    display: block;
    left: 0;
    position: relative;
    width: 3rem;
    height: 0.5rem;
    transition: all 0.3s ease 0s;
    margin-right: 5rem;
    z-index: 5;
  }

  .menu__burger span,
.menu__burger::before,
.menu__burger::after {
    position: absolute;
    left: 0;
    width: 2.5rem;
    height: 0.2rem;
    background-color: #fff;
    transition: all 0.3s ease 0s;
  }

  .menu__burger::before,
.menu__burger::after {
    content: "";
  }

  .menu__burger::before {
    top: -0.5rem;
  }

  .menu__burger::after {
    bottom: -0.2rem;
  }

  .menu__burger._active span {
    width: 0;
    height: 0;
  }

  .menu__burger._active::before {
    top: 0.2rem;
    transform: rotate(-45deg) translate(0px, 0.2rem);
  }

  .menu__burger._active::after {
    bottom: 0.2rem;
    transform: rotate(45deg) translate(0px, 0.2rem);
  }

  .menu__list {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    color: #ffffff;
    background-color: #3b3938;
    padding: 2rem;
    padding-top: 2rem;
    transition: left 0.3s ease 0s;
    overflow: auto;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .menu__list._active {
    left: 0;
  }

  .menu__item,
.menu__link {
    margin: 0;
    padding: 1rem;
    width: 100%;
    font-size: 1.7rem;
  }

  .menu__item:last-child,
.menu__link:last-child {
    padding-bottom: 0;
  }
}
@media (max-width: 463px) {
  .nav__social-list {
    display: none;
  }

  .nav {
    padding-top: 1rem;
  }
}
/* ----- menu media end ----- */
/* ----- pc / touch ----- */
body._touch .menu-item {
  display: flex;
  align-items: center;
}
body._touch .menu-link {
  flex: 1 1 auto;
}

/* ----- pc / touch end ----- */
.button {
  width: 13.6rem;
  height: auto;
  font-size: 1.1rem;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  border: 0.2rem solid #fff;
  background: 0 0;
  margin: 0 auto;
}
.button--gradient {
  padding: 1rem;
  border: 0.2rem solid transparent;
  border-image: linear-gradient(90deg, #ab3b67 0%, #f67b6b 49%);
  border-image-slice: 1;
}
.button--entrance {
  padding: 0.7rem;
  height: 3.2rem;
}
.button--gallery {
  height: 3.2rem;
  padding: 0.7rem;
}

.header {
  background-image: url("../img/header_bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}
.header__nav {
  margin: 0 auto;
}
.header__intro {
  padding-top: 9rem;
}

.nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.menu__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  font-size: 0.8rem;
  font-weight: 900;
  text-transform: uppercase;
  list-style: none;
  margin: 0 auto;
}
.menu__item {
  padding: 1rem;
}
.menu__link {
  display: block;
  color: #fff;
  width: 100%;
}

.social-list {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}
.social-list__item {
  padding: 1rem;
  width: 3rem;
  height: 3rem;
}
.social-list__link {
  display: block;
  width: 100%;
  text-decoration: none;
  width: 1rem;
  height: 1rem;
}
.social-list__link > picture, .social-list__link > img {
  width: 100%;
}

.intro__text {
  font-size: 1.3rem;
  text-transform: uppercase;
}
.intro__main-slogan {
  margin-top: 0.5rem;
}
.intro__button {
  margin-top: 3rem;
  margin-bottom: 13rem;
}

.main-slogan__slogan {
  font-size: 3.6rem;
  line-height: 4rem;
  font-weight: 900;
  text-transform: uppercase;
  background: #ab3b67;
  background: linear-gradient(90deg, #ab3b67 0%, #f67b6b 49%);
  background-clip: text;
  color: transparent;
}
.main-slogan__slogan--footer {
  font-size: 1.3rem;
  line-height: normal;
  color: #fff;
}
.main-slogan__text {
  font-size: 1.3rem;
  text-transform: uppercase;
  margin-top: 0.7rem;
}
.main-slogan__text--footer {
  font-size: 1rem;
  color: #fff;
}

.entrance {
  margin: 0 auto;
  padding: 2.3rem 0;
}
.entrance__title {
  font-size: 1.7rem;
  font-weight: 900;
  text-transform: uppercase;
  margin: 0 auto;
  text-align: center;
}
.entrance__text {
  font-size: 1rem;
  text-align: center;
  padding: 1.8rem;
}
.entrance__gallery {
  margin: 0 auto;
}

.music-gallery {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.music-gallery__item {
  margin: 2rem;
  width: 16.4rem;
  height: 26.8rem;
  padding: 1.4rem;
  border-radius: 0.5rem;
  background-size: 18.5rem 30rem, cover;
  background-repeat: no-repeat, no-repeat;
  background-position: center, center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.music-gallery__item--cost-100 {
  background-image: url("../img/entrance-item--cost-100.png"), linear-gradient(90deg, #ab3b67 0%, #f67b6b 49%);
}
.music-gallery__item--cost-50 {
  background-image: url("../img/entrance-item--cost-50.png"), linear-gradient(90deg, #ab3b67 0%, #f67b6b 49%);
}
.music-gallery__title {
  font-size: 1.4rem;
  text-transform: uppercase;
}
.music-gallery__cost {
  margin-top: 10.2rem;
  font-size: 2.7rem;
  text-transform: uppercase;
}
.music-gallery__cost sup {
  font-size: 1.4rem;
}
.music-gallery__button {
  margin-top: 2.2rem;
}
.music-gallery__dude {
  font-size: 0.7rem;
  margin-top: 1.2rem;
  padding-left: 1.5rem;
  background-image: url("../img/entrance-item__dude.png");
  background-size: 1rem 1rem;
  background-repeat: no-repeat;
  background-position: left;
}
.music-gallery__description {
  margin-top: 1.4rem;
  line-height: normal;
  font-size: 0.55rem;
  text-align: center;
}

.trajectory {
  background-image: url("../img/trajectory_bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  padding: 3rem 0;
}
.trajectory__wrapper {
  width: 50%;
  margin-left: auto;
}
@media (max-width: 1000px) {
  .trajectory__wrapper {
    width: 70%;
  }
}
@media (max-width: 768px) {
  .trajectory__wrapper {
    width: 90%;
  }
}
.trajectory__title {
  font-size: 1.7rem;
  font-weight: 900;
  text-transform: uppercase;
}
.trajectory__text {
  font-size: 1rem;
  line-height: 1.2rem;
  margin-top: 2.5rem;
}
.history {
  background-image: url("../img/history-list--points.png");
  background-repeat: no-repeat;
  background-size: auto 90%;
  background-position: 5.2rem 0.5rem;
}
@media (max-width: 500px) {
  .history {
    background-position: 0 0.2rem;
  }
}
.history__row {
  display: flex;
  margin-top: 2rem;
  justify-content: space-between;
  align-items: flex-start;
}
@media (max-width: 500px) {
  .history__row {
    flex-direction: column;
  }
}
.history__year {
  font-size: 1.5rem;
  font-weight: 300;
  padding-top: 0.5rem;
}
@media (max-width: 500px) {
  .history__year {
    padding-left: 3rem;
  }
}
.history__description {
  padding-left: 5.2rem;
}
@media (max-width: 500px) {
  .history__description {
    padding-left: 2rem;
  }
}

.gallery {
  padding: 2.3rem 0;
}
.gallery__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.gallery__title {
  font-size: 1.7rem;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
}
.gallery__section {
  margin: 0 auto;
  margin-top: 2.3rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.gallery__button {
  margin: 0 auto;
  margin-top: 2.3rem;
}
.gallery__item {
  max-width: 17rem;
}
.gallery__img {
  width: 100%;
}

.contacts {
  min-height: 45rem;
  position: relative;
  background-image: url("../img/contacts__pointer.png"), url("../img/contacts__map.jpg");
  background-size: 2.7rem 3rem, cover;
  background-repeat: no-repeat, no-repeat;
  background-position: center, center;
}
.contacts__title {
  clip-path: circle(0 at 50% 50%);
}
.contacts__popup {
  position: absolute;
  bottom: 0;
  left: 8rem;
}
@media (max-width: 1200px) {
  .contacts__popup {
    left: 2rem;
  }
}
@media (max-width: 400px) {
  .contacts__popup {
    left: 0.2rem;
    width: 95%;
    padding: 0.4rem;
  }
}

.popup {
  width: calc(100vw - 30%);
  height: auto;
  max-width: 45rem;
  min-height: 10rem;
  padding: 4rem;
  color: #000;
  background-color: #dedede;
  opacity: 0.85;
  border-radius: 0.3rem;
  text-align: center;
}
.popup__title {
  font-size: 1.7rem;
  font-weight: 900;
  text-transform: uppercase;
}
.popup__main-text, .popup__text {
  line-height: 1.6rem;
}
.popup__main-text {
  margin-top: 0.3rem;
}
.popup__text {
  font-size: 0.8rem;
}

.footer {
  background: #5328a0;
  background: linear-gradient(90deg, #5328a0 0%, #602a6c 50%, #772e0b 100%);
}
.footer__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 3rem;
}
.footer__main-slogan {
  flex: 1 1 auto;
}